import _ from 'lodash'

export default {
    /**
     * 
     * @returns {*}
     */
    checkLogin(ctx) {
        let status = null;
        if (ctx.session.userInfo && ctx.session.userInfo.userId) {
            status = true;
        } else {
            status = false;
            ctx.body = { code: 401, data: null, msg: '' }
        }
        return status
    },
    /**
     * 
     * @param req
     * @param res
     * @returns {*}
     */
    queryValidate(ctx) {
        let query = ctx.query
        if (typeof(query) !== 'object') {
            return null
        }
        // query = Object.keys(query).length > 0 ? query : null

        return _.omit(query, ['token'])
    },

    /**
     * 
     * @param req
     * @param res
     * @returns {*}
     */
    bodyValidate(ctx) {
        let body = ctx.request.body
        if (typeof(body) !== 'object') {
            return null
        }
        return _.omit(body, ['token'])
    },

    /**
     * token 
     * @param req
     * @param res
     * @returns {*}
     */
    tokenValidate(ctx) {
        let body = ctx.body
        let query = ctx.query
        if (typeof(body) !== 'object' || typeof(query) !== 'object') {
            return null
        }

        if (query.hasOwnProperty('token')) {
            return query
        }

        return body
    }


}


