import _ from 'lodash'
import { wraper } from '../utils/wraper'

export default {

    /**
     * 
     * @param {*} model
     */
    fetch(model, sortType = 'update_time', conditions = {}) {
        return new Promise((resolve, reject) => {
            model.fetch((err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            }, sortType, conditions)
        })
    },
    /**
     * 
     */
    fetchPage(model, skip, limit, sortType, conditions) {
        return new Promise((resolve, reject) => {
            model.fetchPage(parseInt(skip), parseInt(limit), (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            }, sortType, conditions)
        })
    },

    /**
     *  
     */
    refFetchPage(model, skip, limit, sortType, conditions) {
        return new Promise((resolve, reject) => {
            model.refFetchPage(parseInt(skip), parseInt(limit), (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            }, sortType, conditions)
        })
    },

    /**
     * 
     * @param {*} model
     * @param {*} conditions
     */
    find(model, conditions) {
        return new Promise((resolve, reject) => {
            model.findInfo(conditions, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    },

    /**
     * 
     * @param {*} model
     * @param {*} id
     */
    findById(model, id) {
        return new Promise((resolve, reject) => {
            model.findById(id, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    },
    /**
     *  
     * @param {*} model
     * @param {*} id
     */
    refFindById(model, id) {
        return new Promise((resolve, reject) => {
            model.refFindById(id, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })

    },

    /**
     * id
     * @param {*} model
     * @param {*} ids
     */
    findByMulId(model, ids) {
        return new Promise((resolve, reject) => {
            model.findByMulId(ids, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    },

    /**
     * 
     * @param {*} model
     * @param {*} conditions
     */
    count(model, conditions) {
        return new Promise((resolve, reject) => {
            model.count(conditions, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    },

    /**
     * 
     * @param {*} model
     * @param {*} doc
     */
    create(model, doc) {
        //
        doc = wraper(model, doc)
        return new Promise((resolve, reject) => {
            model.createInfo(doc, (err, data) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(data)
                }
            })
        })
    },

    /**
     * 
     * @param {*} model
     * @param {*} id
     * @param {*} params
     */
    update(model, id, params) {
        return new Promise((resolve, reject) => {
            model.updateInfo(id, params, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    },
    /**
     * 
     * @param {*} model
     * @param {*} id
     * @param {*} params
     */
    updateBase(model, conditions = {}, update = {}, options = {}) {
        return new Promise((resolve, reject) => {
            model.update(conditions, update, options, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    },


    /**
     * by id
     * @param {*} model
     * @param {*} id
     */
    remove(model, id) {
        return new Promise((resolve, reject) => {
            model.removeInfo(id, (err, doc) => {
                if (err) {
                    reject(err)
                } else {
                    resolve(doc)
                }
            })
        })
    }

}


