import _ from 'lodash'
import Model from '../models'
import Entity from '../service/entity'

export default class CategoryControl{

    /**
     * 
     */
    async categoryList() {
        let res = null;
        let doc = await Entity.fetch(Model.category).catch(e => {
            res = {
                code: 500,
                data: null,
                msg: 'query error'
            }
        })

        // 
        if (doc) {
            res = {
                code: 200,
                data: doc,
                msg: ''
            }
        }

        //
        return new Promise((resolve) => {
            resolve(res)
        })
    }

    /**
     * 
     * @param {*} categoryid 
     */
    async categoryDetail(categoryid) {
        let res = null
        let doc = await Entity.findById(Model.category, categoryid).catch(e => {
            res = {
                code: 500,
                data: null,
                msg: 'query error'
            }
        })
        // 
        if (doc) {
            res = {
                code: 200,
                data: doc,
                msg: ''
            }
        }

        //
        return new Promise((resolve) => {
            resolve(res)
        })
    }

    /**
     * 1
     * @param {*} categoryid 
     */
    async updateTotal(categoryid) {
        let res = null
        let doc = await Entity.findById(Model.category, categoryid).catch(e => {
            res = {
                code: 500,
                data: null,
                msg: 'query error'
            }
        })
        // 
        if (doc) {
            res = {
                code: 200,
                data: {},
                msg: ''
            }

            await Entity.update(Model.category, categoryid, {
                total : doc.total + 1
            }).catch(e => {
                res = {
                    code: 500,
                    data: null,
                    msg: 'update fail'
                }
            })
        }

        //
        return new Promise((resolve) => {
            resolve(res)
        })
    }
}

export const categoryControl = new CategoryControl()



